.icon-dinner{width:20px;height:20px;background:url("../../assets/img/dinner-icon.png");background-size:contain}.main-cont{width:100%}.shedule-day{transition:.3s}.shedule-day .cab{font-size:13px;color:#fff;background:#22539c;padding:2px 7px;border-radius:3px;width:53px;height:22px;margin-left:7px;transition:.3s;margin-bottom:10px}.shedule-day .sign{width:20px;height:20px;border-radius:50%;background:#81b4ff;display:flex;justify-content:center;align-items:center;font-size:11px;color:#fff}.sheldue-day-cont{width:100%;justify-content:center;display:flex}.content{margin-top:50px;display:flex;width:100%;justify-content:center}.content .couples{width:80%}.content .couples .item{display:flex;transition:.4s;width:100%}.content .couples .item .num{font-size:48px;font-weight:bold;width:60px;color:#9b9b9b;border-right:1px solid #adadad;cursor:pointer}.content .couples .active-item{transition:.4s}.content .couples .active-item .num{color:#22539c}.content .couples .active-item .couple{background:#dfeeff}.content .couples .active-item .couple .name{color:#22539c}.content .couples .active-item .couple .cab{color:#22539c;background:#fff}.content .couples .item:hover .couple{box-shadow:0px 7px 18px rgba(0,0,0,.39) !important}.content .couples .active-item .couple::after{content:"текущаяя пара";margin-left:auto;margin-right:auto;padding-top:10px;padding-bottom:8px;font-size:14px;line-height:0;background:#22539c;color:#fff;padding-left:10px;padding-right:10px;border-radius:10px 10px 0 0}.content .couples .one-couple{display:grid}.content .couples .one-couple .time{margin-bottom:10px}.content .couples .no-lesson{display:flex;justify-content:center;align-items:center;font-weight:bold;font-size:20px;color:#b3b3b3}.content .couples .couple{box-shadow:0px 0px 8px rgba(0,0,0,.25);min-height:150px;width:90%;margin-bottom:30px;margin-left:21px;border-radius:5px;padding-left:12px;padding-right:12px;transition:.3s}.content .couples .couple .sub-item .cont{display:flex}.content .couples .couple .